	module.exports = {"pathContext":{"docHtml":"<h1>filename-regex <a href=\"https://www.npmjs.com/package/filename-regex\"><img src=\"https://img.shields.io/npm/v/filename-regex.svg?style=flat\" alt=\"NPM version\"></a> <a href=\"https://npmjs.org/package/filename-regex\"><img src=\"https://img.shields.io/npm/dm/filename-regex.svg?style=flat\" alt=\"NPM monthly downloads\"></a>  <a href=\"https://npmjs.org/package/filename-regex\"><img src=\"https://img.shields.io/npm/dt/filename-regex.svg?style=flat\" alt=\"NPM total downloads\"></a> <a href=\"https://travis-ci.org/regexhq/filename-regex\"><img src=\"https://img.shields.io/travis/regexhq/filename-regex.svg?style=flat&#x26;label=Travis\" alt=\"Linux Build Status\"></a></h1>\n<blockquote>\n<p>Regular expression for matching file names, with or without extension.</p>\n</blockquote>\n<h2>Install</h2>\n<p>Install with <a href=\"https://www.npmjs.com/\">npm</a>:</p>\n<pre><code class=\"language-sh\">$ npm install --save filename-regex\n</code></pre>\n<h2>Usage</h2>\n<pre><code class=\"language-js\">var regex = require('filename-regex');\n\n'a/b/c/d.min.js'.match(regex());\n//=> match[0] = 'd.min.js'\n\n'a/b/c/.dotfile'.match(regex());\n//=> match[0] = '.dotfile'\n</code></pre>\n<h2>About</h2>\n<h3>Contributing</h3>\n<p>Pull requests and stars are always welcome. For bugs and feature requests, <a href=\"../../issues/new\">please create an issue</a>.</p>\n<h3>Building docs</h3>\n<p><em>(This project's readme.md is generated by <a href=\"https://github.com/verbose/verb-generate-readme\">verb</a>, please don't edit the readme directly. Any changes to the readme must be made in the <a href=\".verb.html\">.verb.md</a> readme template.)</em></p>\n<p>To generate the readme, run the following command:</p>\n<pre><code class=\"language-sh\">$ npm install -g verbose/verb#dev verb-generate-readme &#x26;&#x26; verb\n</code></pre>\n<h3>Running tests</h3>\n<p>Running and reviewing unit tests is a great way to get familiarized with a library and its API. You can install dependencies and run tests with the following command:</p>\n<pre><code class=\"language-sh\">$ npm install &#x26;&#x26; npm test\n</code></pre>\n<h3>Author</h3>\n<p><strong>Jon Schlinkert</strong></p>\n<ul>\n<li><a href=\"https://github.com/jonschlinkert\">github/jonschlinkert</a></li>\n<li><a href=\"https://twitter.com/jonschlinkert\">twitter/jonschlinkert</a></li>\n</ul>\n<h3>License</h3>\n<p>Copyright © 2017, <a href=\"https://github.com/jonschlinkert\">Jon Schlinkert</a>.\nReleased under the <a href=\"LICENSE\">MIT License</a>.</p>\n<hr>\n<p><em>This file was generated by <a href=\"https://github.com/verbose/verb-generate-readme\">verb-generate-readme</a>, v0.4.3, on April 28, 2017.</em></p>","docPath":"argo-ci/node_modules/filename-regex/readme","proj":"argo-ci"}}

